import axios from "axios";
import { config } from "../../config/api";
import { useQuery } from "react-query";

export const useFaqs = (props) => {
  const queryInstance = useQuery(["faqs"], async () => {
    const { data } = await axios.get(`${config.apiGateWay}/faq`);
    console.log(data);
    return data.faqs;
  });
  return queryInstance;
};

export default useFaqs;
