import { useTransition } from "react-spring";
import { animated } from "@react-spring/web";
import React, { useState } from "react";
import styled from "styled-components";
import TestimonialItem from "./TestimonialItem";

const TestimonialSection = ({ className }) => {
  const data = [{ id: 1 }, { id: 2 }, { id: 3 }, { id: 4 }];
  const [slidePage, setSlidePage] = useState(0);
  const next = () => {
    setSlidePage((prev) => (prev + 1) % 2);
  };
  const back = () => {
    setSlidePage((prev) => Math.abs(prev - 1) % 2);
  };

  const [animatedSlide] = useTransition(
    data.slice(slidePage * 2, slidePage * 2 + 2),
    {
      from: { transform: "translate3d(0,-40px,0)", opacity: 0 },
      enter: { transform: "translate3d(0,0px,0)", opacity: 1 },
      leave: { transform: "translate3d(0,-40px,0)", opacity: 0 },
      key: (item) => item.id,
      reset: true,
      expires: true,
      trail: 200,
      config: { precision: 0.1 },
      exitBeforeEnter: true,
    },
    [slidePage]
  );
  return (
    <section className={`${className} testimonial container mt-5`}>
      <div className="header d-flex justify-content-between align-items-center">
        <div className="my-4 fs__title fw-bold">Người dùng nói gì về EraVietnam</div>
        <div className="arrows d-flex">
          <div className="arrows__btn left" onClick={() => back()}>
            <i class="fas fa-arrow-left"></i>
          </div>
          <div className="arrows__btn right" onClick={() => next()}>
            <i class="fas fa-arrow-right"></i>
          </div>
        </div>
      </div>

      <div className="row flex-column flex-lg-row">
        {animatedSlide((style, item, t, i) => {
          return (
            <animated.div className="col-12 col-lg-6" style={style}>
              <TestimonialItem trail={i * 200} />
            </animated.div>
          );
        })}
      </div>
    </section>
  );
};

export default styled(TestimonialSection)`
  .arrows {
    column-gap: 8px;
    &__btn {
      padding: 16px 20px;
      transition: all 0.6s cubic-bezier(0.61, 0.22, 0.23, 1);
      border-width: 1px;
      border: 1px solid ${(props) => props.theme.colorSecondary};
      position: relative;
      /* overflow: hidden; */
      i {
        transition: all 0.6s cubic-bezier(0.61, 0.22, 0.23, 1);
      }
      &.left {
        &::before {
          transition: all 0.6s cubic-bezier(0.61, 0.22, 0.23, 1);
          font-family: "Font Awesome 5 Free";
          font-weight: 900;
          -webkit-font-smoothing: antialiased;
          font-style: normal;
          font-variant: normal;
          text-rendering: auto;
          line-height: 1;
          content: "\f060";
          position: absolute;
          right: 0;
          top: 0;
          bottom: 0;
          left: 0;
          display: flex;
          justify-content: center;
          align-items: center;
          transform: translateX(50%) scale(0);
          opacity: 0;
        }
        &:hover {
          i {
            transform: translateX(-50%) scale(0);
          }
        }
      }
      &.right {
        &::before {
          transition: all 0.6s cubic-bezier(0.61, 0.22, 0.23, 1);
          font-family: "Font Awesome 5 Free";
          font-weight: 900;
          -webkit-font-smoothing: antialiased;
          font-style: normal;
          font-variant: normal;
          text-rendering: auto;
          line-height: 1;
          content: "\f061";
          position: absolute;
          right: 0;
          top: 0;
          bottom: 0;
          left: 0;
          display: flex;
          justify-content: center;
          align-items: center;
          transform: translateX(-50%) scale(0);
          opacity: 0;
        }
        &:hover {
          i {
            transform: translateX(50%) scale(0);
          }
        }
      }
      &:hover {
        &::before {
          opacity: 1;
          transform: translateX(0) scale(1);
        }
      }
      i {
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
  .slick-track {
    display: flex;
  }
  .testimonial {
  }
`;
