import { useQuery } from "react-query";
import { config } from "../../config/api";
import useAxios from "./useAxios";

export const useBlogs = (take = 4) => {
  const Axios = useAxios();
  const queryInstance = useQuery(["blogs", { take }], async () => {
    const { data } = await Axios.get(`${config.apiGateWay}/blog?take=${take}`);
    console.log(data);
    return data;
  });
  return queryInstance;
};
