import { animated, config, to, useSpring } from "react-spring";
import React from "react";
import styled from "styled-components";

const TestimonialItem = ({ className, trail = 0 }) => {
  // const [
  //   {
  //     avaOpacity,
  //     avaScale,
  //     descOpacity,
  //     descScale,
  //     headerClipPath,
  //     headerOpacity,
  //     headerTransform,
  //     nameOpacity,
  //     nameScale,
  //     textOpacity,
  //     textTransform,
  //   },
  // ] = useSpring(
  //   {
  //     to: [
  //       {
  //         headerClipPath: "inset(0 0 0 0)",
  //         headerOpacity: 1,
  //         headerTransform: "translateY(0)",
  //         textOpacity: 1,
  //         textTransform: "translateY(0)",
  //       },
  //       { avaScale: 1, avaOpacity: 1, nameScale: 1, nameOpacity: 1 },

  //       { descScale: 1, descOpacity: 1 },
  //     ],
  //     from: {
  //       headerClipPath: "inset(100% 0 0 0)",
  //       headerOpacity: 0,
  //       headerTransform: "translateY(10px)",
  //       textOpacity: 0,
  //       textTransform: "translateY(10px)",
  //       avaScale: 0,
  //       avaOpacity: 0,
  //       nameScale: 0,
  //       nameOpacity: 0,
  //       descScale: 0,
  //       descOpacity: 0,
  //     },
  //     config: config.wobbly,
  //   },
  //   []
  // );
  const { headerClipPath, headerOpacity, headerTransform } = useSpring({
    headerClipPath: "inset(0 0 0 0)",
    headerOpacity: 1,
    headerTransform: "translateY(0)",
    from: {
      headerClipPath: "inset(100% 0 0 0)",
      headerOpacity: 0,
      headerTransform: "translateY(10px)",
    },
    config: config.wobbly,
    delay: trail + 200,
  });
  const { textOpacity, textTransform } = useSpring({
    textOpacity: 1,
    textTransform: "translateY(0)",
    from: { textOpacity: 0, textTransform: "translateY(10px)" },
    config: config.wobbly,
    delay: trail + 500,
  });
  const { avaOpacity, avaScale } = useSpring({
    avaScale: 1,
    avaOpacity: 1,
    from: { avaScale: 0, avaOpacity: 0 },
    config: config.wobbly,
    delay: trail + 1000,
  });
  const { nameOpacity, nameTransform, nameClipPath } = useSpring({
    nameOpacity: 1,
    nameClipPath: "inset(0 0 0 0)",
    nameTransform: "translateY(0)",
    from: { nameOpacity: 0, nameTransform: "translateY(10px)", nameClipPath: "inset(100% 0 0 0)" },
    config: config.gentle,
    delay: trail + 1300,
  });
  const { descOpacity, descClipPath, descTransform } = useSpring({
    descOpacity: 1,
    descClipPath: "inset(0 0 0 0)",
    descTransform: "translateY(0)",
    from: { descOpacity: 0, descTransform: "translateY(10px)", descClipPath: "inset(100% 0 0 0)" },
    config: config.gentle,
    delay: trail + 1600,
  });

  return (
    <div className={`${className} d-flex flex-column`}>
      <animated.div
        className="testimonial__header"
        style={{ clipPath: headerClipPath, opacity: headerOpacity, transform: headerTransform }}
      >
        Lorem ipsum dolor sit amet consectetur adipisicing elit
      </animated.div>
      <animated.div
        className="testimonial__main"
        style={{ opacity: textOpacity, transform: textTransform }}
      >
        <p className="testimonial__content">
          "Lorem ipsum dolor sit amet consectetur adipisicing elit. Necessitatibus eos rem eum
          blanditiis, quis reprehenderit!Lorem ipsum dolor sit amet consectetur adipisicing elit.
          Necessitatibus eos rem eum blanditiis, quis reprehenderit!"
        </p>
      </animated.div>
      <div className="d-flex align-content-center testimonial__user">
        <animated.img
          src="/images/meow.jpg"
          alt=""
          className="testimonial__user__img"
          style={{
            opacity: avaOpacity,
            transform: to([avaScale], (scale) => `scale(${scale})`),
          }}
        />
        <animated.div className="testimonial__user_info">
          <animated.div
            className="testimonial__user__name fw-bolder"
            style={{
              opacity: nameOpacity,
              transform: nameTransform,
              clipPath: nameClipPath,
            }}
          >
            Tên
          </animated.div>
          <animated.div
            className="testimonial__user__desc"
            style={{
              opacity: descOpacity,
              transform: descTransform,
              clipPath: descClipPath,
            }}
          >
            Senior designer at design studio
          </animated.div>
        </animated.div>
      </div>
    </div>
  );
};

export default styled(TestimonialItem)`
  padding: 32px;
  margin: 16px;
  row-gap: 32px;
  background-color: #fafafa;
  box-shadow: 8px 8px 12px 4px rgba(0, 0, 0, 0.2);
  border-radius: 16px;
  position: relative;
  &::before {
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    -webkit-font-smoothing: antialiased;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    line-height: 1;
    content: "\f10e";
    position: absolute;
    right: 0;
    top: 0;
    width: 64px;
    height: 64px;
    color: white;
    border-radius: 50%;
    background-color: ${(props) => props.theme.colorSecondary};
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translate(50%, -50%);
  }
  .testimonial {
    &__header {
      font-weight: 900;
      font-size: 32px;
      color: #ccc06b;
    }
    &__content {
      font-weight: 600;
      opacity: 0.6;
    }
    &__user {
      column-gap: 16px;
      &__img {
        border-radius: 50%;
        height: 50px;
        width: 50px;
        object-fit: cover;
      }
      &__desc {
        opacity: 0.4;
      }
    }
  }
`;
