import React from "react";
import styled from "styled-components";
import useFaqs from "../../common/hooks/useFaqs";
import FAGItem from "./FAGItem";

const QNASection = ({ className }) => {
  const { data: faqs } = useFaqs();
  return (
    <section className={`${className} home__questions mt-5 container mb-2`} id="faq">
      <p className="fs__title text-center fw-bold">Câu hỏi thường gặp</p>
      <div className="mt-5">
        {faqs?.map((faq) => (
          <FAGItem key={faq.id} question={faq.name} answer={faq.desc} />
        ))}
      </div>
    </section>
  );
};

export default styled(QNASection)`
  h5 {
    color: #ccc;
    font-size: 1rem;
    margin-bottom: 1rem;
    font-weight: 600;
  }
`;
