import React, { useEffect, useRef } from "react";
import ScrollReveal from "scrollreveal";
import styled from "styled-components";

const HeroSection = ({ className }) => {
  const animateRefs = useRef([]);

  useEffect(() => {
    ScrollReveal().reveal(animateRefs.current, {
      duration: 700,
      reset: true,
      interval: 500,
    });
    return () => {};
  }, []);
  return (
    <div className={`${className} py-5 bg__custom--1`}>
      <section className="container p-3">
        <div className="row align-items-center">
          <div className="home__content col-12 col-lg-3">
            <header
              className="home__content__header fs__title--main"
              ref={(el) => (animateRefs.current[0] = el)}
            >
              Thần số học là gì?
            </header>
            <p className="home__content__sub mt-4" ref={(el) => (animateRefs.current[1] = el)}>
              Lorem ipsum dolor sit, amet consectetur adipisicing elit. Consequuntur voluptatum
              veritatis, dicta voluptate soluta ex voluptates enim tempora magnam, quasi repellat
              rem illum! Voluptate, enim voluptas. Placeat pariatur laudantium nobis.
            </p>
          </div>
          <div className="home__video col-12 col-lg-8 offset-0 offset-lg-1"></div>
        </div>
      </section>
    </div>
  );
};

export default styled(HeroSection)`
  background-color: #225d5a;
  .home {
    &__content {
      color: #fafafa;
      &__header {
        color: #fafafa;
        font-size: 3em;
        line-height: 60px;
        font-weight: bold;
      }
      &__sub {
        font-size: 1em;
      }
    }
    &__video {
      background-color: gray;
      height: 400px;
    }
  }
`;
