import { config, useChain, useSpring, useTrail } from "react-spring";
import { animated } from "@react-spring/web";
import React, { useEffect, useRef } from "react";
import { useInView } from "react-intersection-observer";
import ScrollReveal from "scrollreveal";
import styled from "styled-components";

const SubHeroSection = ({ className }) => {
  const { ref, inView } = useInView({ threshold: 1, triggerOnce: true });
  const animateRefs = useRef([]);

  const [listAnimated, listRef] = useTrail(
    4,
    {
      config: config.wobbly,
      transform: inView ? "scale(1) translateY(0)" : "scale(0) translateY(20px)",
      opacity: inView ? 1 : 0,
    },
    [inView]
  );
  const [imgAnimated, imgRef] = useSpring(
    {
      config: config.slow,
      clipPath: inView ? "inset(0 0 0 0)" : "inset(100% 0 0 0)",
    },
    [inView]
  );
  useChain([imgRef, listRef], [0, 0.5]);
  useEffect(() => {
    ScrollReveal().reveal(animateRefs.current, {
      duration: 700,
      reset: true,
      interval: 500,
    });
    return () => {};
  }, []);
  return (
    <section className={`${className} container`}>
      <div className="home__me my-5 text-center">
        <p className="fs__title fw-bold" ref={(el) => (animateRefs.current[0] = el)}>
          Chúng tôi là ai?
        </p>
        <p className="col-12 col-lg-5 mx-auto" ref={(el) => (animateRefs.current[1] = el)}>
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Iure dolorum nesciunt excepturi
          porro in consequuntur accusamus, ducimus velit! Ipsum expedita et itaque. Optio est
          numquam voluptas necessitatibus maxime, eligendi quasi?
        </p>
      </div>
      <div className="home__pros row justify-content-around align-items-center mt-5" ref={ref}>
        <div className="home__pros__img col-12 col-lg-6">
          <animated.img
            style={imgAnimated}
            src="https://www.topclass.com.vn/articles/wp-content/uploads/2021/01/Ly-Qui-Khanh-Tien-bac-khong-mua-duoc-tai-nang.jpg"
            alt="Ưu đãi của Era"
          />
        </div>
        <div className="col-12 col-lg-5 offset-lg-1">
          <p className="fs__title fw-bold">
            Lorem ipsum dolor sit amet consectetur adipisicing elit. Neque distinctio obcaecati
            modi!
          </p>
          <ul>
            {listAnimated.map((props, index) => (
              <animated.li style={props} key={index} className="fs__detail">
                Lorem ipsum dolor sit amet consectetur adipisicing elit. At totam aut
                exercitationem.
              </animated.li>
            ))}
          </ul>
        </div>
      </div>
    </section>
  );
};

export default styled(SubHeroSection)`
  .home {
    &__pros {
      &__img {
        height: 400px;
        > img {
          height: inherit;
          width: 100%;
          object-fit: cover;
        }
      }
    }
  }
`;
